<template>
  <div id="register" class="formPage flex align center">
      <div class="userForm shadownItemBox flex  between stretch" >
        <div class="leftBg"></div>

        <div class="mainBox">
            <div class="title">WMC 重置密码</div>

            <div class="formBox">

              <el-tabs v-model="activeName">
                <el-tab-pane label="电话号码" name="phone"></el-tab-pane>
                <el-tab-pane label="邮箱号码" name="email"></el-tab-pane>
              </el-tabs>

              <div class="inputGroup flex align">
                <getArea v-show="activeName=='phone'" :default="form.area" @setVal="x=>form.area=x" />
                <input type="text" :placeholder="`请输入${activeName=='phone'?'电话':'邮箱'}号码`"  v-model="form.phoneOrEmail">
              </div>
              <!-- inputGroup -->


              <div class="inputGroup flex align">
                <input type="text" placeholder="请输入验证码" v-model="form.code">
                <GetCode :codeData="{number:form.phoneOrEmail,type:'pwd'}"/>
              </div>
              <!-- inputGroup -->

              <div class="inputGroup flex align">
                <input type="password" placeholder="请输入新密码" v-model="form.password">
              </div>
              <!-- inputGroup -->

              <div class="inputGroup flex align">
                <input type="password" placeholder="再次输入密码" v-model="form.passwordRe">
              </div>
              <!-- inputGroup -->


              <div class="themBtn" @click="getPwd" v-loading="load">重置密码</div>

              <div class="other flex align between" v-if="!userInfo">
                <div></div>
                <div><span>已有账号？</span>&nbsp;&nbsp;<a @click="gopage('/login')">立即登录</a></div>
              </div>


            </div>
            <!-- formBox -->

        </div>
        <!-- main -->

      </div>
      <!-- userForm -->
  </div>
  <!-- index -->
</template>

<script>
  import {
    mapMutations,
    mapState,
    mapActions
  } from 'vuex'
  import {
    changePassword
  } from '../data/apis';
  import getArea from '@com/common/getArea'
  import GetCode from '@com/common/getCode'

  
  export default {
    data() {
      return {
        activeName:"phone",
        form:{
          "area": "86",
          "code": "110112",
          "invitation": "8888",
          "password": "123123123",
          "passwordRe":"123123123",
          "phoneOrEmail": "15288888888"
        },

        load:false
      }
    },
    components:{GetCode,getArea},
    computed: {
      ...mapState(['userInfo'])
    },
    methods: {
      ...mapMutations(['setUserInfo']),
      ...mapActions(['actionUserInfo']),
      getPwd(){//账户密码
         if (!this.form.phoneOrEmail) {
          return this.$message.error('请输入电话或邮箱号码');
        }
        if (!this.form.code) {
          return this.$message.error('请输入验证码');
        }
        if (!this.form.password) {
          return this.$message.error('请输入密码');
        }
        if (!this.form.passwordRe) {
          return this.$message.error('请再次输入密码');
        }
        if (this.form.passwordRe !== this.form.password) {
          return this.$message.error('密码不一致');
        }

        let res = Object.assign({},this.form);
        
        if(this.activeName=='email'){
          res.area = null;
        }

        this.load = true;
        changePassword(res).then(v=>{
          this.load = false;
          this.$message.success(v.message);
          this.gopage_re('/login')
        }).catch(e=>{
          this.load = false;
        })
      }//getPwd
    },
    mounted() {
      
    }, //mounted
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../styles/Val";
  
  #register{
    .inputGroup {
      margin:30px 0;
      input{
        padding-left: 0;
      }
    }
  }
  
</style>



